<template>
  <v-app>
    <top-menu/>
    <v-content>
      <router-view/>
    </v-content>
    <modal-dialog @closeModalSignOut="onCloseModalSignOut" @closeModal="onCloseModal"></modal-dialog>
  </v-app>
</template>

<script>
import Vue from 'vue'
import Menu from '@/components/Menu.vue'
import ModalDialog from '@/components/ModalDialog.vue'

export default {
  name: 'app',
  created() {
  },
  components: {
    modalDialog: ModalDialog,
    topMenu: Menu
  },
  methods: {
    async onCloseModalSignOut() {
      this.$store.dispatch('session/logout')
        .catch(e => console.log('logout!!!', e))
    },
    async onCloseModal() {
      this.$router.push({path: '/'})
    }
  }
}
</script>


