<template>
  <v-toolbar app height="48px" v-if="userName">
    <!-- <img :src="require('../assets/img/logo.svg')" class="img-responsive" height="24"/> -->
    
    <v-btn icon to="/">
      <v-icon x-large>home</v-icon>
    </v-btn>
    
    <v-spacer v-if="checkInStatus < 1"></v-spacer>
    <!-- checkInStatus.2 => ids verified -->
    <v-btn icon to="/preCheckIn" v-if="checkInStatus < 1">
      <v-icon x-large>contact_mail</v-icon>
    </v-btn>

    <v-spacer v-if="checkInStatus < 3"></v-spacer>
    <!-- checkInStatus.3 => checkIn Done -->
    <v-btn icon to="/checkIn" v-if="checkInStatus < 3">
      <v-icon x-large>supervisor_account</v-icon>
    </v-btn>

    <v-spacer v-if="checkInStatus >= 1"></v-spacer>
    <v-btn icon v-if="checkInStatus >= 1">
      <v-icon x-large>directions</v-icon>
    </v-btn>

    <v-spacer v-if="checkInStatus == 3"></v-spacer>
    <v-btn icon v-if="checkInStatus == 3" @click="showModalDialog()">
      <v-icon x-large>vpn_key</v-icon>
    </v-btn>

    <v-spacer></v-spacer>
    <v-btn icon @click.native="toggleFullScreen">
      <v-icon x-large>fullscreen</v-icon>
    </v-btn>

    <v-spacer></v-spacer>
    <v-menu :nudge-width="100">
      <v-toolbar-title slot="activator">
        <v-icon x-large>language</v-icon>
      </v-toolbar-title>

      <v-list>
        <v-list-tile
          v-for="item in items"
          :key="item"
          @click="switchLocale(item)"
        >
          <v-list-tile-title>{{ $t( "menu." + item) }}</v-list-tile-title>
          <flag :iso="item" />
        </v-list-tile>
      </v-list>
    </v-menu>

    <v-spacer></v-spacer>
    <v-btn icon @click.native="logout">
      <v-icon x-large>exit_to_app</v-icon>
    </v-btn>

  </v-toolbar>
</template>

<script>
import Vue from 'vue'
import { mapGetters, mapState, mapActions } from 'vuex'
import FlagIcon from 'vue-flag-icon'
import { EventBus } from '@/eventBus'
import * as utils from '@/utils'

Vue.use(FlagIcon)

export default {
  name: 'Menu',
  data: () => ({
    items: [
      'gb', 'hk', 'jp'
    ],
    ok: false
  }),
  created () {
    console.log('created checkInStatus', this.checkInStatus)
  },
  mounted () {
    console.log('mounted checkInStatus', this.checkInStatus)
  },
  computed: mapState({
    userName: state => state.session.userName,
    ...mapGetters('checkIn', {
      checkInStatus: 'checkInStatus'
    })
  }),
  methods: {
    switchLocale (item) {
      this.$i18n.locale = item
    },
    showModalDialog() {
      EventBus.$emit('ModalDialog', { showDialog: true });
    },
    toggleFullScreen() {
      try {
        utils.toggleFullScreen()  
      } 
      catch (e) {
        window.alert(e)
      }
      
    },
    ...mapActions({
      logout: 'session/logout',
    })
  },
}
</script>